import * as React from 'react';
import { BlogCard, Cite } from '../styles/styles';
import { memo } from 'react';
import { INode } from '../pages';
import PostTitle from './post/PostTitle';

// @ts-ignore
const BlogList = ({ blog }) => {
  console.log(blog.props[0].frontmatter);
  const blogPosts = blog.props.map((post: INode) => (
    <BlogCard key={post.id}>
      <PostTitle text={post.frontmatter.title} />
      <p>{post.excerpt}</p>
      <Cite>{post.frontmatter.date}</Cite>
    </BlogCard>
  ));

  return <div>{blogPosts}</div>;
};

export default memo(BlogList);
